import React from 'react'

const Toast = ({ msg, handleShow, bgColor, lang = 'en' }) => {
  return (
    <div
      className={`toast show position-fixed text-light ${bgColor}`}
      style={{
        top: '5px',
        right: '5px',
        minWidth: '280px',
        zIndex: 50,
        direction: lang === 'ar' ? 'rtl' : 'ltr',
        textAlign: lang === 'ar' ? 'right' : 'left',
      }}
    >
      <div className="toast-header text-light" style={{ background: 'inherit' }}>
        <strong className="mr-auto text-light">
          {msg.title}
        </strong>

        <button
          type="button"
          className="ml-2 mb-1 close text-light"
          data-dismiss="toast"
          style={{ outline: 'none' }}
          onClick={handleShow}
        >
          &times;
        </button>
      </div>

      <div className="toast-body">
        {msg.body}
      </div>
    </div>
  )
}

export default Toast
